let changeLook = ['所有人可见','仅自己可见']

import uniNavBar from '../../components/uni-nav-bar/uni-nav-bar.vue';
import uploadImg from '../../components/common/upload-img.vue';
import uniPopup from '../../components/uni-popup/uni-popup.vue'
export default {
	components:{
		uniNavBar,
		uploadImg,
		uniPopup
	},
	data() {
		return {
			changeLookText:'所有人可见',
			text:'',
			imageList:[],
			isShowPopup:true,  //是否立即显示
			isget:false, //用户是否点击保存组
		};
	},
	onLoad() {
		uni.getStorage({
		    key: 'caogao_text',
		    success:res => {
		        this.text = res.data;
		    }
		});
	},
	// 返回按钮监听
	onBackPress() {
		// 判断内容是否为空
		if(this.text == '' && this.imageList.length < 1) return;
		if(!this.isget){
			this.baocun();
			return true;
		}
	},
	methods:{
		
		// 是否保存草稿操作
		baocun(){
			uni.showModal({
				content: '是否要保存为草稿？',
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					// 确定
					if(res.confirm){
						uni.setStorage({
						    key: 'caogao_text',
						    data: this.text,
						    success: res => {
						        console.log('success');
						    }
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
						uni.setStorage({
						    key: 'caogao_text',
						    data: '',
						    success: res => {
						        console.log('success');
						    }
						});
					}
					this.isget=true;
					uni.navigateBack({
						delta: 1
					});
				},
			});
		},
		// 返回监听
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		
		// 发布
		submit(){
			console.log('发布')
		},
		
		// 获取上传的图片
		getImageList(imageList){
			this.imageList = imageList;
		},
		
		// 隐私设置
		changeLook(){
			uni.showActionSheet({
				itemList:changeLook,
				success:(res) => {
					this.changeLookText = changeLook[res.tapIndex]
				},
				fail:(err) => {
					console.log(err)
				}
			})
		},
		
		// 关闭弹出层
		closePopup(){
			  this.$refs.popup.close()
		}
		
	},
	
}
